import React from 'react';
import marked from 'marked';
import hljs from 'highlightjs';
import blogApi from '../../apis/blogApi';

// const ArticleCreate = () => {
//     return (
//         <div></div>
//     );
// };

class ArticleCreate extends React.Component {
    state = { title: '', markdown: '' }

    onFormSubmit = event => {
        const ACCESSTOKEN = localStorage.getItem('access');

        blogApi.post("/articles/", {
            title: this.state.title,
            body: this.state.markdown
        },
        {
            headers: {
                Authorization: `JWT ${ACCESSTOKEN}`,
            }
        });
        this.setState({title: '', markdown: ''})
        window.location.assign("../")
    }
    
    buttonPressSubmit = event => {
        event.preventDefault();
        this.onFormSubmit();
    }

    componentDidMount() {
        marked.setOptions({
            highlight: function(code, lang) {
              return hljs.highlightAuto(code, [lang]).value;
            }
        });
    }

    render(){
        return (
            <div>
                    <div className="ui two column stackable grid">
                        <div className="column">
                            <div className="ui segment">
                                <div className="ui form">
                                    <form className="ui form" onSubmit={this.onFormSubmit}>
                                        <div className="field">
                                            <label>Title</label>
                                            <input 
                                                type="text"
                                                value={this.state.title}
                                                onChange={(e) => this.setState({title: e.target.value})}
                                            />
                                        </div>
                                        <div className="field">
                                            <label>Body</label>
                                            <textarea
                                                value={this.state.markdown}
                                                onChange={(e) => this.setState({markdown: e.target.value})}
                                            />
                                        </div>
                                        <button className="fluid ui button" onClick={this.buttonPressSubmit}>Submit</button>
                                    </form>
                                </div>
                            </div>
                        </div>
                        <div className="column">
                            <div className="ui segment">
                                <div dangerouslySetInnerHTML={{__html: marked(this.state.markdown)}}></div>
                            </div>
                        </div>
                    </div>
            </div>
        );
    }
}

export default ArticleCreate;